import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../state/actions/index';

import InformationTab from '../components/landing-page/InformationTab';
import MonthlyIncome from '../components/landing-page/MonthlyIncome';


class TransactionHistoryApp extends Component {

  componentDidMount() {
    const accessToken = localStorage.getItem("accessToken");
    // const accessToken = "73d56c1845f521f0af44cc1215662475";

    if (accessToken) {
      this.props.accessToken(accessToken);
      this.props.remoteGetUserProfile(accessToken);
    }

  }

  render() {

    var details = null;

    if (this.props.loading != null && this.props.userProfile != null) {
      details = <div>
        <div className="col-lg-8">
          <InformationTab />
        </div>
        <div className="col-lg-4">
          <MonthlyIncome />
        </div>
      </div>
    }

    return (

      <div className="row">
        {details}
        {/* {console.log(this.props.accessToken)} */}
      </div>

    );
  }
}

const mapStateToProps = state => {
  return {
    userProfile: state.transactionDisputeReducer.userProfile,
    loading: state.transactionDisputeReducer.loading,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
    accessToken: (accessToken) => dispatch(actions.accessToken(accessToken)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(TransactionHistoryApp);



