import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../state/actions/index';
import CircularProgress from '@material-ui/core/CircularProgress';



class InformationTab extends Component {
    componentDidMount = () => {
        const userProfile = this.props.userProfile;
        const accessToken = this.props.accessToken;
        this.props.remoteGetNotificationList(accessToken, userProfile.name);
    }

    clickHandeler = (caseId) => {
        Promise.resolve(
            localStorage.setItem("caseId", caseId)
        )
            .then(function () {
                window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-customer/en/case_detail_secure.page';
            })
        //   window.location = 'http://localhost:8181/fsi-credit-card-dispute-customer/en/landing_secure.page';
    }

    render() {
        const userProfile = this.props.userProfile;
        const notificationList = this.props.notificationList;
        console.log(JSON.stringify(notificationList));

        var notificationBt = null;

        if (notificationList != null) {
            notificationBt = notificationList.map((item) => {

                const date = new Date(parseInt(item['case-started-at']));
                const options = { year: 'numeric', month: 'numeric', day: 'numeric' };
                var status = null;
                var color = null;

                if (item['case-status'] === 1) {
                    status = 'OPEN';
                    color = '#0092E1'
                    return (
                        <div style={{ paddingBottom: "5px" }}>
                            <div style={{ background: color, color: '#fff', height: '35px', width: '98%' }} onClick={(caseId) => this.clickHandeler(item['case-id'])}>
                                <p style={{ paddingTop: '12px', paddingLeft: '10px', fontSize: '8px' }}>Case ID: {item['case-id']} | {date.toLocaleDateString('en-US', options)} | {status}</p>
                            </div>
                        </div>
                    );
                } 
                // else if (item['case-status'] === 2) {
                //     status = 'CLOSE';
                //     color = '#19AA89'
                // } else if (item['case-status'] === 3) {
                //     status = 'CANCELLED';
                //     color = '#F85555'
                // }

            })
        }

        if (this.props.loading) {
            notificationBt =
                <div style={{ marginLeft: "40%", marginTop: "10%" }}>
                    <CircularProgress />
                </div>;
        }

        if (this.props.error) {
            notificationBt =
                <div>
                    <p>Can't connect to the server</p>
                </div>;
        }


        return (
            <div className="ibox float-e-margins ">
                <div className="ibox-title bluetop">
                    <h2>Diamond Travel Card by Red Hat</h2>
                </div>
                <div className="ibox-content">
                    <div className="row">
                        <div className="col-md-4" style={{ borderRight: 'solid 2px #DADADA', borderRightStyle: 'dashed', minHeight: '190px' }}>
                            <h3><b>Minimum Payment</b></h3>
                            <p style={{ fontSize: "16px" }}>{userProfile.minCredit}</p>
                            <button className="btn btn-primary" style={{ marginTop: "15px", backgroundColor: "#19AA89", color: "#fff" }}>Make Payment</button>
                            <p style={{ paddingTop: '40px', color: "#19AA89" }}><b>Replace Card</b></p>
                        </div>
                        <div className="col-md-4" style={{ borderRight: 'solid 2px #DADADA', borderRightStyle: 'dashed', minHeight: '190px' }}>
                            <p><b>Last Statement Balance</b>
                                <br />{userProfile.lstatmentb}</p>
                            <p style={{ paddingTop: "20px" }}><b>Next Statement Due</b>
                                <br />{userProfile.nxtStmnt}</p>
                        </div>
                        <div className="col-md-4">
                            <p><b>Open Cases</b></p>
                            <div style={{ overflowY: 'scroll', height: '130px' }}>
                                <br />{notificationBt}
                            </div>
                            <p style={{ color: "#19AA89" }}><b>Achieved Cases</b></p>
                        </div>
                    </div>
                </div>
            </div>

        );
    }
}
const mapStateToProps = state => {
    return {
        userProfile: state.transactionDisputeReducer.userProfile,
        notificationList: state.transactionDisputeReducer.notificationList,
        accessToken: state.transactionDisputeReducer.accessToken,
        loading: state.transactionDisputeReducer.loading,
        error: state.transactionDisputeReducer.error,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
        remoteGetNotificationList: (accessToken, name) => dispatch(actions.remoteGetNotificationList(accessToken, name))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(InformationTab);



